* {
  box-sizing: border-box;
}

body {
  --background-color: white;
  --text-color: black;
  --elements-color: white;
  background-color: var(--background-color);
  color: var(--text-color);
  margin: 0;
  font-family: Nunito, sans-serif;
}

body.dark {
  --background-color: #202c37;
  --text-color: white;
  --elements-color: #2b3945;
}

main {
  padding: 24px;
}

a {
  color: inherit;
  text-decoration-line: none;
}

.header-container {
  background-color: var(--elements-color);
  padding-inline: 24px;
  box-shadow: 0 2px 4px #0000001a;
}

.header-content {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin-inline: auto;
  display: flex;
}

.title {
  font-weight: 800;
}

.theme-changer {
  cursor: pointer;
}

.countries-container {
  margin-top: 16px;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 64px;
  max-width: 1200px;
  margin-inline: auto;
  display: grid;
}

.country-card {
  background-color: var(--elements-color);
  border-radius: 8px;
  width: 250px;
  padding-bottom: 24px;
  transition: all .2s ease-in-out;
  display: inline-block;
  overflow: hidden;
  box-shadow: 0 0 8px #0003;
}

.country-card:hover {
  transform: scale(1.03);
  box-shadow: 0 0 16px #0003;
}

.card-title {
  margin-block: 16px;
  font-size: 24px;
}

.card-text {
  padding-inline: 16px;
}

.card-text p {
  margin-block: 8px;
}

.country-card img {
  width: 100%;
}

.search-filter-container {
  margin-inline: auto;
  justify-content: space-between;
  gap: 24px;
  max-width: 1200px;
  margin-top: 16px;
  margin-bottom: 48px;
  display: flex;
}

.search-container {
  color: var(--text-color);
  background-color: var(--elements-color);
  border-radius: 4px;
  width: 100%;
  max-width: 350px;
  padding-left: 24px;
  overflow: hidden;
  box-shadow: 0 0 8px #00000026;
}

.search-container input {
  background-color: inherit;
  color: inherit;
  border: none;
  outline: none;
  width: 90%;
  padding: 16px;
}

.search-container input::placeholder {
  color: #999;
}

.filter-by-region {
  background-color: var(--elements-color);
  color: var(--text-color);
  border: none;
  border-radius: 4px;
  outline: none;
  width: 250px;
  padding: 16px;
  box-shadow: 0 0 8px #00000026;
}

@media (width <= 768px) {
  .countries-container {
    justify-content: center;
  }

  .title {
    font-size: 16px;
  }

  .header-content p {
    font-size: 12px;
  }

  .search-filter-container {
    flex-direction: column;
  }
}

.shimmer-wrapper {
  flex-wrap: wrap;
  gap: 50px;
  display: flex;
}

.shimmer-card {
  background-color: #e0e0e0;
  border-radius: 10px;
  width: 200px;
  height: 300px;
  position: relative;
  overflow: hidden;
}

.shimmer {
  background: linear-gradient(90deg, #f0f0f0 25%, #fff 50%, #f0f0f0 75%) 0 0 / 200% 100%;
  width: 100%;
  height: 100%;
  animation: 1.5s infinite shimmer;
}

.shimmer-text {
  background-color: #e0e0e0;
  border-radius: 5px;
  height: 16px;
  margin-top: 10px;
}

.shimmer-image {
  background-color: #d0d0d0;
  border-radius: 10px 10px 0 0;
  width: 100%;
  height: 150px;
}

@keyframes shimmer {
  0% {
    background-position: -100% 0;
  }

  100% {
    background-position: 100% 0;
  }
}

.country-details-container {
  margin-inline: auto;
  max-width: 1200px;
  margin-top: 32px;
}

.back-button {
  cursor: pointer;
  border-radius: 4px;
  padding: 6px 24px;
  box-shadow: 0 0 4px #0003;
}

.country-details {
  align-items: center;
  gap: 64px;
  margin-top: 64px;
  display: flex;
}

.country-details img {
  border: 1px solid #ddd;
  width: 40%;
}

.details-text {
  flex-flow: column wrap;
  gap: 12px 64px;
  max-height: 180px;
  display: flex;
}

.details-text p {
  margin: 0;
}

.border-countries {
  flex-wrap: wrap;
  align-items: flex-start;
  gap: 8px;
  margin-top: 48px;
  display: flex;
}

.border-countries a {
  border-radius: 4px;
  padding: 4px 16px;
  font-size: 14px;
  box-shadow: 0 0 4px #0003;
}

@media (width <= 940px) {
  .country-details {
    flex-direction: column;
    align-items: flex-start;
    gap: 16px;
    margin-top: 32px;
  }

  .details-text {
    max-height: unset;
  }

  .country-details img {
    width: 100%;
    max-width: 400px;
  }
}

.skeleton {
  background: linear-gradient(90deg, #f0f0f0 25%, #e0e0e0 50%, #f0f0f0 75%) 0 0 / 200% 100%;
  animation: 1.5s infinite shimmer;
}

.skeleton-container {
  gap: 20px;
  padding: 20px;
  display: flex;
}

.skeleton-image {
  border-radius: 8px;
  width: 200px;
  height: 150px;
}

.skeleton-text {
  flex: 1;
}

.skeleton-line {
  border-radius: 4px;
  width: 100%;
  height: 16px;
  margin-bottom: 10px;
}

.skeleton-line.short {
  width: 50%;
}

.skeleton-line.medium {
  width: 75%;
}

.skeleton-line.long {
  width: 90%;
}

.not-found-container {
  color: #721c24;
  text-align: center;
  background-color: #f8d7da;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-size: 1.5rem;
  display: flex;
}

.not-found-message {
  background-color: #f8d7da;
  border: 1px solid #f5c6cb;
  border-radius: .25rem;
  padding: 1rem 2rem;
  box-shadow: 0 0 10px #0000001a;
}

.loading-container {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.loading-spinner {
  border: 8px solid #0000001a;
  border-top-color: #3498db;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  animation: 1s linear infinite spin;
}

@keyframes spin {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}
